import * as React from "react";
import "./assets/styles/style.css";
import { BrowserRouter as Router, Link, useLocation } from "react-router-dom";

function Blog() {
  return (
    <div className="container">
      <div className="col-md-2">
        <Link to="#angular">
          <h1>Angular</h1>
        </Link>
      </div>
      <div className="col-md-2">
        <Link to="#react">
          <h1>React</h1>
        </Link>
      </div>
      <div className="col-md-2">
        <Link to="#vue">
          <h1>Vue</h1>
        </Link>
      </div>
      <div className="col-md-2">
        <h1>Ios</h1>
      </div>
      <div className="col-md-2">
        <h1>Andriod</h1>
      </div>

      <div className="blog-content col-md-12" id="angular">
        Angular
      </div>
      <div className="blog-content col-md-12" id="react">
        react
      </div>
      <div className="blog-content col-md-12" id="vue">
        vue
      </div>
    </div>
  );
}

export default Blog;
